@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
  --header-font: "Titan One", sans-serif;
  --main-font: "DM Sans", sans-serif;
  --coral-pink-headers: #ffa5ba;
  --dark-red-brand-color: #d41443;
  --bgc-products_first-color: #ffb8ca;
  --bgc-products_second-color: #f0d1a5;
  --bgc-products_third-color: #c2e297;
  --white-text-color: #fff;
  --light-coral-background-color: #fff5f6;
  --black-color: #000;
  --gray-color: #e1e1e1;
  --brown-color: #907e82;
}

body {
  font-family: var(--main-font);
}

.products-header-wrap {
  font-family: var(--header-font);
  margin-bottom: 8px;
}

.products-first-header {
  color: var(--coral-pink-headers);
  text-transform: uppercase;
  font-weight: 400;
  margin: 0;
  text-align: center;
  font-family: "Titan One", sans-serif;
  font-size: 18px;
  line-height: normal;
  letter-spacing: 1.2px;
  line-height: normal;
}

.products-second-header {
  color: var(--dark-red-brand-color);
  text-transform: uppercase;
  font-size: 34px;
  margin: 0;
  text-align: center;
  font-family: "Titan One", sans-serif;
  font-size: 34px;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 3.48px;
}

.container {
  width: 100%;
  margin: auto;
  padding: 0 20px;
}

.products-section-container {
  padding: 60px 20px;
}

.products-card-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 0px;
  align-items: center;
}

.products-card {
  width: 280px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  padding-left: 32px;
  padding-right: 32px;
  border-radius: 24px;
  border: none;
  transition-property: transform, background-color, box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic bezier(0.4, 0, 0.2, 1);
  position: relative;
  margin-top: 80px;
}

.products-card:hover {
  transform: scale(1.05);
  box-shadow: 0 1px 2px rgba(196, 56, 56, 0.207), 0 2px 4px rgba(0, 0, 0, 0.07), 0 4px 8px rgba(0, 0, 0, 0.07), 0 8px 16px rgba(0, 0, 0, 0.07), 0 16px 32px rgba(0, 0, 0, 0.07), 0 32px 64px rgba(0, 0, 0, 0.07);
}

.products-card-image {
  width: 280px;
  height: 280px;
  flex-shrink: 0;
  border: none;
}

.products-card-items-wrapper {
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: center;
  justify-content: center;
  padding-left: 32px;
  padding-right: 32px;
  border-radius: 24px;
  padding-bottom: 40px;
}

.products-card-header {
  color: var(--white-text-color);
  text-align: center;
  font-family: "Titan One", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.8px;
  text-transform: uppercase;
}

.products-card-icon {
  fill: var(--white-text-color);
}

.products-card-paragraph {
  color: var(--white-text-color);
  text-align: center;
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 176%;
  letter-spacing: 0.56px;
}

.product-photos {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  display: relative;
}

.icecoffee {
  background-color: var(--bgc-products_second-color);
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic bezier(0.4, 0, 0.2, 1);
}

.icecoffee:hover {
  background-color: rgba(203, 156, 91, 0.6941176471);
}

.icecream {
  background-color: var(--bgc-products_first-color);
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic bezier(0.4, 0, 0.2, 1);
}

.icecream:hover {
  background-color: #e5a7b8;
}

.milkshake {
  background-color: var(--bgc-products_third-color);
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic bezier(0.4, 0, 0.2, 1);
}

.milkshake:hover {
  background-color: #b2ce8c;
}

.products-card-button {
  border-radius: 50%;
  background-color: var(--white-text-color);
  min-width: 40px;
  min-height: 40px;
  border: none;
  transition-property: transform, background-color, border-radius;
  transition-duration: 250ms;
  transition-timing-function: cubic bezier(0.4, 0, 0.2, 1);
}

.products-card:hover .products-card-button {
  transform: rotate(-90deg) scale(1.05);
  background-color: var(--dark-red-brand-color);
  border-radius: 15px;
}

.products-card-button-icon {
  fill: #d41443;
}

.products-card:hover .products-card-button-icon {
  fill: white;
}

@media screen and (728px <= width < 1200px) {
  .products-header-wrap {
    margin-bottom: 20px;
  }
  .products-first-header {
    font-size: 26px;
  }
  .products-second-header {
    font-size: 48px;
  }
  .products-card-list {
    flex-direction: row;
    gap: 16px;
    justify-content: center;
  }
  .products-section-container {
    padding-left: 32px;
    padding-right: 32px;
  }
  .products-card {
    width: 224px;
  }
  .products-card-image {
    width: 224px;
    height: 224px;
  }
  .products-card-paragraph {
    width: 188px;
    height: 150px;
  }
}
@media screen and (min-width: 1200px) {
  .products-first-header {
    font-size: 30px;
  }
  .products-second-header {
    font-size: 58px;
  }
  .products-card-list {
    flex-direction: row;
    gap: 32px;
    justify-content: center;
  }
  .products-card-paragraph {
    font-size: 16px;
  }
  .products-card-header {
    font-size: 30px;
  }
  .products-section-container {
    padding-left: 16px;
    padding-right: 16px;
  }
  .products-header-wrap {
    margin-bottom: 20px;
  }
  .products-card {
    width: 368px;
  }
  .products-card-image {
    width: 368px;
    height: 394px;
  }
}
